.container {
  height: 100vh;
  width: 100%;
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;

  .flex {
    display: flex !important;
    align-items: center;
  }

  .scroll {
    bottom: 10px;
    position: absolute;
    z-index: 2;
    width: 100px;
    left: 50%;
    margin-left: -50px;
  }

  .scroll::before {
    animation: bounce 1s ease infinite;
    bottom: 2rem;
    color: rgb(148, 148, 148);
    content: "╲╱";
    font-size: 2rem;
    height: 4rem;
    left: 50%;
    letter-spacing: -1px;
    line-height: 4rem;
    margin-left: -3rem;
    opacity: 0.8;
    position: absolute;
    text-align: center;
    width: 6rem;
  }

  @media screen and  (max-width: 560px) {
    .scroll {
      margin-left: 0px;
    }

    .scroll::before {
      left: auto;
    }
  }

  @keyframes bounce {
    50% {
      transform: translateY(-50%);
    }
  }

  .text-container {
    width: 50%;
    height: 100%;
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow-x: hidden;

    .text {
      padding: 1em 5em;
    }

    .button {
      margin-top: 2em;
      padding: 0 5.3em;
    }
  }

  .image {
    width: 50%;
    height: 100%;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .purple {
    z-index: -1;
    overflow: hidden;
    position:absolute;
    top:30%;
    left:40%;
    width:0;
    padding:0;
    animation: explodePurple 1s forwards;
    &.light {
      filter: blur(150px) brightness(1.5);
    }
    &.dark {
      filter: blur(170px) brightness(0.7);
    }
  }
  .blue {
    z-index: -1;
    overflow: hidden;
    position:absolute;
    top:30%;
    right:40%;
    width:0;
    padding:0;
    animation: explodeBlue 1s forwards;
    &.light {
      filter: blur(120px) brightness(1.2);
    }
    &.dark {
      filter: blur(150px) brightness(0.7);
    }
  }
  .red {
    z-index: -1;
    overflow: hidden;
    position:absolute;
    top:30%;
    right:40%;
    width:0;
    padding:0;
    animation: explodeRed 1s forwards;
    &.light {
      filter: blur(150px) brightness(1.5);
    }
    &.dark {
      filter: blur(170px) brightness(0.7);
    }
  }
  .orange {
    z-index: -2;
    overflow: hidden;
    position:absolute;
    top:30%;
    left:40%;
    width:0;
    padding:0;
    animation: explodeOrange 1s forwards;
    &.light {
      filter: blur(200px);
    }
    &.dark {
      filter: blur(200px) brightness(0.6);
    }
  }
  .grey {
    position: absolute;
    z-index: -3;
    top: 25%;
    left: 35%;
    width: 40%;
    overflow: hidden;
    &.light {
      filter: blur(150px) brightness(1.5);
    }
    &.dark {
      filter: blur(150px) brightness(1);
    }
  }

  @keyframes explodePurple {
    0% {
        width:40%
     }
    100%{
        width:45%;
        top: 55vh;
        left: -50px;
        padding:5px;
     }
  }

  @keyframes explodeBlue {
    0% {
        width:40%
     }
    100%{
        width:45%;
        top: -150px;
        right: -10px;
        padding:5px;
     }
  }

  @keyframes explodeRed {
    0% {
        width:40%
     }
    100%{
        width: 40%;
        top: 55vh;
        right: 0px;
        padding:5px;
     }
  }

  @keyframes explodeOrange {
    0% {
        width:40%
     }
    100%{
        width: 40%;
        top: -220px;
        left: -100px;
        padding:5px;
     }
  }

  @media screen and (max-width: 1500px) { 
    .image {
      img {
        width: 75%;
      }
    }
  }

  @media screen and (max-width: 960px) {
    .purple {
      position: absolute;
      z-index: -1;
      top: 60vh;
      left: -10%;
      filter: blur(120px);
      width: 70%;
      animation: none;
      overflow: hidden;
    }
    .blue {
      position: absolute;
      z-index: -1;
      top: 0;
      left: 35%;
      filter: blur(120px);
      width: 60%;
      animation: none;
      overflow: hidden;
    }
    .red {
      position: absolute;
      z-index: -1;
      top: 60vh;
      left: 35%;
      filter: blur(120px);
      width: 60%;
      animation: none;
      overflow: hidden;
    }
    .orange {
      position: absolute;
      z-index: -2;
      top: 0;
      left: 0;
      filter: blur(120px);
      width: 60%;
      animation: none;
      overflow: hidden;
    }
    .grey {
      position: absolute;
      z-index: -3;
      top: 25%;
      left: 20%;
      filter: blur(100px);
      width: 60%;
      overflow: hidden;
    }
    .image {
      display: none;
    }
    .text-container {
      width: 100%;
    }
  }

  @media screen and (max-width: 650px) {
    .text-container {
      width: 100%;
      .text {
        padding: 0 3em;
      }
      .button {
        padding: 1em 3em;
      }
    }

    .image {
      display: none;
    }
  }

  @media screen and (max-width: 500px) {

    .text-container {
      .text {
        padding: 0 1em;
      }
      .button {
        padding: 1em 1em;
        // width: 60%;
      }
    }
  }
}


