$accent-color: #4f05ff;
$text-color: rgb(39, 39, 39);

.about {
  margin-top: 15em;
  display: flex !important;
  flex-direction: column;
  justify-content: center;
  position: relative;

  .about-image {
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    width: 53%;

    // .loading {
    //   position: relative;
    //   background-color: #ffffff34;
    //   height: 50%;
    //   width: 50%;

    //   &::after {
    //     display: block;
    //     content: "";
    //     position: absolute;
    //     width: 50%;
    //     height: 100%;
    //     transform: translateX(-50%);
    //     background: linear-gradient(
    //       90deg,
    //       transparent,
    //       rgba(219, 219, 219, 0.493),
    //       transparent
    //     );
    //     animation: loading 1.5s infinite;
    //   }
    // }

    // @keyframes loading {
    //   100% {
    //     transform: translateX(100%);
    //   }
    // }
  }

  .blue {
    position: absolute;
    z-index: -1;
    top: 60vh;
    left: -200px;
    filter: blur(150px);
  }
  .aqua {
    position: absolute;
    z-index: -1;
    top: -100px;
    right: -200px;
    filter: blur(170px) brightness(1.2);
  }
  .orange {
    position: absolute;
    z-index: -1;
    top: 60vh;
    right: -200px;
    filter: blur(180px) brightness(1.5);
  }

  @media screen and (max-width: 960px) {
    .about-image {
      width: 100%;
      height: 400px;
    }

    .blue {
      position: absolute;
      z-index: -1;
      top: 30vh;
      left: 0;
      filter: blur(120px);
      width: 60%;
      overflow: hidden;
    }
    .aqua {
      position: absolute;
      z-index: -1;
      top: 0;
      left: 0;
      filter: blur(120px) brightness(1.2);
      width: 60%;
      overflow: hidden;
    }
    .orange {
      position: absolute;
      z-index: -1;
      top: 70vh;
      left: 0;
      filter: blur(120px) brightness(1.5);
      width: 70%;
      overflow: hidden;
    }
  }
}

@media screen and (max-width: 660px) {
  .about {
    margin-top: 3em;
  }
}
